import 'piral/polyfills';
import * as actions from './actions/incrementTestValue';
import { renderInstance, setupLocalizer } from 'piral';
import { layout, errors } from './layout';

// change to your feed URL here (either using feed.piral.cloud or your own service)
const feedUrl = 'http://localhost:9000/api/v1/pilet';

declare module 'piral-core/lib/types/custom' {
  interface PiralCustomActions {
    incrementTestValue: void;
  }

  interface PiralCustomState {
    mainVar: number;
  }
}

const { root } = renderInstance({
  layout,
  errors,
  actions,
  requestPilets() {
    return fetch(feedUrl)
      .then(res => res.json())
      .then(res => res.items);
  },
});

root.on('alert', ({name, value}) => console.log())